const CurrentTime = () => {
  const [currentTime, setCurrentTime] = useState(new Date());

  useEffect(() => {
    // 创建一个新的定时器，在每一秒钟执行一次函数来更新时间状态。
    const timerId = setInterval(() => {
      setCurrentTime(new Date());
    }, 1000);

    // 清除定时器以防止内存泄漏。
    return () => clearInterval(timerId);
  }, []);

  return (
    <div className={styles.containerTop}>
      <div style={{ float: 'left' }}>
        current time：{currentTime.toLocaleString()}
      </div>
      <div style={{ float: 'right' }}>
        AI Control： <Switch defaultChecked size="small" />
      </div>
    </div >
  );
};
const style = { background: '#0092ff', padding: '8px 0' };
const Hvac = () => {

  const location = useLocation();
  let initf = location.search.split('?')[1].split("=")[1];
  const [flag, setFlag] = useState(initf);


  let cfProgress = 0;
  const [cfProgressS, setCfProgress] = useState(0);
  const [tt, SetTT] = useState(19);

  // 空调调节：临时的
  const [flagKt, setFlagKt] = useState('');
  const display = displayData(flag, flagKt, tt, cfProgressS);



  useEffect(() => {

    // 创建一个新的定时器，在每一秒钟执行一次函数来更新时间状态。
    const timerIdxxx = setInterval(() => {
      if (flag === '3') {
        cfProgress = cfProgress + 30;
        if (cfProgress >= 100) {
          cfProgress = 100;
        }
        setCfProgress(cfProgress);
        SetTT(cfProgress * 0.022 + 19.8)
      }
    }, 3000);

    // 清除定时器以防内存泄漏
    return () => {
      clearInterval(timerIdxxx)
    };
  }, []); // 空数组依赖项意味着只会在组件挂载和卸载时运行


  useEffect(() => {

    // 设置定时器，在五秒钟后执行回调函数
    let timer1 = setTimeout(() => {
      if (flag === "1") {
        setFlag("2"); // 改变标志变量的值
      }
    }, 10000); // 5000 毫秒等于 5 秒钟


    // const channel = new BroadcastChannel('tab-communication');

    let timer3 = setTimeout(() => {

      // 在即将关闭的标签页中
      window.addEventListener('beforeunload', () => {
        if (flag === '2' || flag === '1') {
          localStorage.setItem(SCENARIO_STORAGE_KEY, Scenario.serverHibernated.toString());
          // channel.postMessage({ type: SCENARIO_STORAGE_KEY, data: Scenario.serverHibernated.toString() });
        }
        if (flag === '4' || flag === '3') {
          localStorage.setItem(SCENARIO_STORAGE_KEY, Scenario.temperatureRecovered.toString());
          // channel.postMessage({ type: SCENARIO_STORAGE_KEY, data: Scenario.temperatureRecovered.toString() });
        }
      });

      window.close();
    }, 15000);

    // 10秒关闭弹窗 modal变成false，flag为4
    let timer2 = setTimeout(() => {
      if (flag === '3') {
        setFlag("4"); // 改变标志变量的值
      }
    }, 12000);

    // 2秒后改成5， 让温度从19改成22
    let timer4 = setTimeout(() => {
      if (flag === '3') {
        setFlagKt("5"); // 改变标志变量的值
      }
    }, 2000);

    // 清除定时器以防内存泄漏
    return () => {
      clearTimeout(timer1);
      clearTimeout(timer2);
      clearTimeout(timer3);
      clearTimeout(timer4);
    };
  }, []); // 空数组依赖项意味着只会在组件挂载和卸载时运行

  return (
    <div>
      {CurrentTime()}
      {display}
    </div>
  );
};

export default Hvac;