import React, { useState, useEffect } from 'react';
import { Modal, Row, Col, Input, Divider, Skeleton } from 'antd';
import styles from './index.less';

const MyModalComponent = ({ flag }) => {

    // 定义模态框的可见性状态

    const [isModalVisible, setIsModalVisible] = useState(false);

    useEffect(() => {


        // 5秒变成modal：ture，
        let timer1 = setTimeout(() => {
            if (flag === '1') {
                setIsModalVisible(true);
            }
        }, 1000);

        // 10秒关闭弹窗 modal变成false，flag为4
        // let timer2 = setTimeout(() => {
        //     if (flag === '1') {
        //         setIsModalVisible(false);
        //     }
        // }, 6000);

        // 清除定时器以防内存泄漏
        return () => {
            clearTimeout(timer1);
            // clearTimeout(timer2);
        };
    }, []); // 空数组依赖项意味着只会在组件挂载和卸载时运行

    return (
        <div>
            {/* 模态框组件 */}

            {
                isModalVisible ?
                    <div>
                        <Divider>Intelligent Analysis Results</Divider>
                        <div style={{ display: 'flex', alignItems: 'center'}}>
                            <div style={{
                                width: '10px',
                                height: '2em',
                                backgroundColor: '#faad14',
                                marginRight: '8px',
                                marginTop: '-6px'
                            }} />
                            <h2>IT Equipment: A 03</h2>
                        </div>
                        {/* <h3>Operation Status</h3> */}
                        <h3>Operation Status: {
                            flag === '1' ?
                                <span className={styles.blinkText} style={{ color: "green" }}>{"Running   ->   Hibernating"}</span>
                                :
                                <span style={{ color: "green" }}>{"Hibernating"}</span>
                        }</h3>
                        <div>
                            <Row gutter={16}>
                                <Col span={12}>
                                    <div className={styles.card} >
                                        <div>CPU Usage</div>
                                        <div>2 %</div>
                                    </div>
                                </Col>
                                <Col span={12}>
                                    <div className={styles.card}>
                                        <div>GPU Usage</div>
                                        <div>1 %</div>
                                    </div>
                                </Col>
                            </Row>
                            <Row gutter={16}>
                                <Col span={12}>
                                    <div className={styles.card} >
                                        <div>RAM Usage</div>
                                        <div>5 %</div>
                                    </div>
                                </Col>
                                <Col span={12}>
                                    <div className={styles.card}>
                                        <div>Storage Usage</div>
                                        <div>5 %</div>
                                    </div>
                                </Col>
                            </Row>
                        </div>
                        <Divider />
                        <h3>Control Command</h3>
                        <div>
                            <h4>Hibernation Threshold  ✔</h4>
                            <Row gutter={16}>
                                <Col span={6}>
                                    <div>CPU Usage  </div>
                                </Col>
                                <Col span={4}>
                                    <div>≤</div>
                                </Col>
                                <Col span={7}>
                                    <div><Input defaultValue={'5%'} /></div>
                                </Col>
                                <Col span={7}>
                                    <div><Input defaultValue={'10 Mins'} /></div>
                                </Col>
                            </Row>

                            <Row gutter={16} style={{ margin: '10px -8px' }}>
                                <Col span={6}>
                                    <div>GPU Usage  </div>
                                </Col>
                                <Col span={4}>
                                    <div>≤</div>
                                </Col>
                                <Col span={7}>
                                    <div><Input defaultValue={'1%'} /></div>
                                </Col>
                                <Col span={7}>
                                    <div><Input defaultValue={'10 Mins'} /></div>
                                </Col>
                            </Row>
                            <Row gutter={16}>
                                <Col span={6}>
                                    <div>RAM Usage  </div>
                                </Col>
                                <Col span={4}>
                                    <div>≤</div>
                                </Col>
                                <Col span={7}>
                                    <div><Input defaultValue={'10%'} /></div>
                                </Col>
                                <Col span={7}>
                                    <div><Input defaultValue={'10 Mins'} /></div>
                                </Col>
                            </Row>

                            {/* <h5>Shutdown Threshold</h5>
                    <Row gutter={16}>
                        <Col span={6}>
                            <div>CPU Usage  </div>
                        </Col>
                        <Col span={4}>
                            <div>≤</div>
                        </Col>
                        <Col span={7}>
                            <div><Input defaultValue={'1%'} /></div>
                        </Col>
                        <Col span={7}>
                            <div><Input defaultValue={'60 Mins'} /></div>
                        </Col>
                    </Row>

                    <Row gutter={16} style={{ margin: '10px -8px' }}>
                        <Col span={6}>
                            <div>GPU Usage  </div>
                        </Col>
                        <Col span={4}>
                            <div>≤</div>
                        </Col>
                        <Col span={7}>
                            <div><Input defaultValue={'0%'} /></div>
                        </Col>
                        <Col span={7}>
                            <div><Input defaultValue={'60 Mins'} /></div>
                        </Col>
                    </Row>
                    <Row gutter={16}>
                        <Col span={6}>
                            <div>RAM Usage  </div>
                        </Col>
                        <Col span={4}>
                            <div>≤</div>
                        </Col>
                        <Col span={7}>
                            <div><Input defaultValue={'5%'} /></div>
                        </Col>
                        <Col span={7}>
                            <div><Input defaultValue={'60 Mins'} /></div>
                        </Col>
                    </Row> */}

                        </div>
                    </div>
                    :
                    <Skeleton />
            }



        </div>
    );
};

export default MyModalComponent;