import G6, { Graph } from '@antv/g6'

export default class FlowGraph {
  public static graph: Graph
  public static init() {

    const container = document.getElementById('container');

    function setContainerHeight() {
      // 计算新的高度
      const newHeight = window.innerHeight - 172;
      // 应用到container上
      container.style.height = `${newHeight}px`;
    }

    // 初始化设置高度
    setContainerHeight();

    // 监听窗口大小改变事件
    window.addEventListener('resize', () => {
      setContainerHeight();
    });

    G6.registerNode(
      'custom-node',
      {
        draw(cfg, group) {
          const padding = 10; // 内边距
          let totalWidth = 0;
          let maxHeight = 0;

          // if (cfg.label) {
          //   const textSize = this.getTextSize(cfg.label, cfg.labelCfg.style.fontSize, cfg.labelCfg.style.fontFamily);
          //   totalWidth += textSize.width + padding * 2;
          //   maxHeight = Math.max(maxHeight, textSize.height);
          // }

          // 添加矩形背景
          const rect = group.addShape('rect', {
            attrs: {
              x: -totalWidth / 2,
              y: -maxHeight / 2,
              width: cfg.width,
              height: cfg.height,
              // radius: 20, // 圆角半径
              fill: 'rgba(0, 255, 0, 0.3)', // 绿色半透明白色
              stroke: 'rgba(0, 255, 0, 0.3)',
              lineWidth: 2,
            },
            name: 'background-shape'
          });

          // 添加文本
          // if (cfg.label) {
          //   const textSize = this.getTextSize(cfg.label, cfg.labelCfg.style.fontSize, cfg.labelCfg.style.fontFamily);
          //   group.addShape('text', {
          //     attrs: {
          //       text: cfg.label,
          //       x: -totalWidth / 2 + cfg.imgSize.width + padding * 2 + textSize.width / 2 - 200, // 文本居中
          //       y: -maxHeight / 2 + (maxHeight - textSize.height) / 2, // 垂直居中
          //       textAlign: 'start',
          //       fontFamily: 'CustomFont', // 使用自定义字体
          //       fontSize: cfg.labelCfg.style.fontSize,
          //       fill: cfg.labelCfg.style.fill,
          //     },
          //     name: 'text-shape'
          //   });
          // }

          return rect; // 返回背景形状作为关键图形
        },
      },
      'single-node'
    );

    this.graph = new Graph({
      container: 'container',
      width: container.clientWidth,
      height: parseInt(container.clientHeight),
      // plugins: [{ type: 'grid-line', size: 30 }],
      /* style for the keyShape */
      layout: {
        type: 'grid' // 使用网格布局，默认情况下节点可能会重叠
      },
      defaultNode: {
        /* node type */
        type: 'rect',
        /* node size */
        size: [50],
        labelCfg: {
          /* label's position, options: center, top, bottom, left, right */
          position: 'center',
          /* label's offset to the keyShape, 4 by default */
          //   offset: 12,
          /* label's style */
          //   style: {
          //     fontSize: 20,
          //     fill: '#ccc',
          //     fontWeight: 500
          //   }
        },
      },
    })
    return this.graph
  }
}