import React, { useState, useEffect, useRef } from 'react'
import { getGraphData } from './getGraphDataFunc';

export default function ({ flag, graph, flagKt, tt }) {

    // 创建对container11 DOM元素的引用
    const containerRef1 = useRef(null);

    useEffect(() => {


        if (!containerRef1.current) return;

        // 获取container11的实际宽度和高度
        const rect = containerRef1.current.getBoundingClientRect();

        let grData = getGraphData(flag, rect.width, rect.height, flagKt, tt);

        try {
            graph && graph.data(grData);
            graph && graph.render();
        } catch (error) {
            console.error(error)
        }


        // 如果需要的话，可以根据获取到的宽度和高度调整graph的配置
        if (graph) {
            graph.changeSize(rect.width, rect.height);
        }

    }, [graph, flag, flagKt, tt])

    return (
        <div ref={containerRef1} id="container" className="x6-graph" />
    )
}