import React, { useState, useEffect } from 'react';
import { Modal, Row, Col, Input, Divider, Skeleton, Steps, Card } from 'antd';
import styles from './index.less';
import { Gauge } from '@ant-design/plots';

const DemoGauge1 = ({ svd }) => {
    const config = {
        width: 200,
        height: 200,
        autoFit: true,
        data: {
            target: svd - 15,
            total: 10,
            name: 'score',
            thresholds: [20 - 15, 24 - 15, 25 - 15],
        },
        "axis": {
            "y": {
                "labelFormatter": (value) => value + 15
            }
        },
        style: {
            textContent: (target, total) => ``,
        },
        legend: false,
        scale: {
            color: {
                range: ['#F4664A', 'green', '#F4664A',],
            },
        },
    };
    return <Gauge {...config} />;
};

const DemoGauge2 = ({ svd }) => {
    const config = {
        width: 200,
        height: 200,
        autoFit: true,
        data: {
            target: svd - 15,
            total: 10,
            name: 'score',
        },
        "axis": {
            "y": {
                "labelFormatter": (value) => value + 15
            },
        },
        style: {
            textContent: (target, total) => ``,
        },
        legend: false,
    };
    return <Gauge {...config} />;
};

const ServerInfo = ({ flag, flagKt, tt, cfProgressS }) => {

    let temp = cfProgressS == undefined ? 100 : cfProgressS;

    let initCurrentStep = 0;
    const [currentStep, setCurrentStep] = useState(initCurrentStep);


    useEffect(() => {

        // 5秒变成modal：ture，
        let timer1 = setInterval(() => {
            if (flag === '3') {
                initCurrentStep++;
                setCurrentStep(initCurrentStep);
                if (initCurrentStep > 2) {
                    clearInterval(timer1)
                }
            }
        }, 1000);

        // 清除定时器以防内存泄漏
        return () => {
            clearInterval(timer1);
        };
    }, []); // 空数组依赖项意味着只会在组件挂载和卸载时运行


    let svd = (flagKt === '5' ? 22 : flag === '3' ? 19 : 19);

    let currs = (flag === '4' ? 4 : currentStep);

    return (
        <div>
            {/* 模态框组件 */}

            <div>
                <Divider>Intelligent Analysis</Divider>
                <div style={{ display: 'flex', alignItems: 'center' }}>
                    <div style={{
                        width: '10px',
                        height: '2em',
                        backgroundColor: '#faad14',
                        marginRight: '8px',
                        marginTop: '-6px'
                    }} />
                    <h2>AC Units</h2>
                </div>

                <Row className={styles.canvas}>
                    <Col span={12}>
                        <Card>
                            <Steps
                                current={currs}
                                percent={60}
                                direction="vertical"
                                items={[
                                    {
                                        title: 'Notify AI Algorithm',
                                        description: "Send alerts to the AI module"
                                    },
                                    {
                                        title: 'Analyze Data',
                                        description: "AI assesses real-time data"
                                    },
                                    {
                                        title: "Output Strategy",
                                        description: 'AI generates an energy-saving plan'
                                    },
                                    {
                                        title: 'Restore State',
                                        description: 'System returns to energy-saving mode'
                                    },
                                ]}
                            />
                        </Card>

                    </Col>
                    <Col span={12}>
                        <Card style={{ margin: '0 10px', textAlign: 'center' }}>
                            <DemoGauge2 svd={svd} />
                            <div style={{ marginTop: '-50px' }}>AC temperature:{svd} °C</div>
                        </Card>
                        <Card style={{ margin: '0 10px', textAlign: 'center' }}>
                            <DemoGauge1 svd={tt} />
                            <div style={{ marginTop: '-50px' }}>ambient temperature: {tt} °C</div>
                        </Card>
                    </Col>
                </Row>

            </div>

        </div>
    );
};

export default ServerInfo;